<template>
	<div class="container">
		<el-row class="tac">
			<el-col :span="24">
				<el-menu
					default-active="1"
					class="el-menu-vertical-demo"
					@select="onHandleSelect"
					background-color="#545c64"
					text-color="#fff"
					active-text-color="#ffd04b">
					<el-menu-item
						:index="String(index + 1)"
						v-for="(item, index) in total">
						<i class="el-icon-menu"></i>
						<span slot="title">{{ item.title }}</span>
					</el-menu-item>
				</el-menu>
			</el-col>
		</el-row>
		<div class="drawer-container">
			<el-drawer
				title="请选择想吃的加入选择"
				:visible.sync="drawer"
				size="60%"
				direction="rtl">
				<FoodList
					:foods="foodsList"
					:type="foodsType"></FoodList>
			</el-drawer>
		</div>
	</div>
</template>
<script>
	import FoodList from "../FoodList";
	import total from "../../data/totals";
	import noodles from "../../data/noodles";
	import donburiRice from "../../data/donburiRice";
	import rices from "../../data/rices";
	import others from "../../data/others";
	export default {
		data() {
			return {
				total: total,
				noodles,
				donburiRice,
				rices,
				others,
				drawer: false,
				foodsType: "", //传递的食物类型
				foodsList: [], //食物列表
			};
		},
		components: {
			FoodList,
		},
		methods: {
			onHandleSelect(e) {
				this.drawer = true;
				console.log(e);
				switch (e) {
					case "1":
						this.foodsType = "noodles";
						this.foodsList = this.noodles;
						break;
					case "2":
						this.foodsType = "donburiRice";
						this.foodsList = this.donburiRice;
						break;
					case "3":
						this.foodsType = "rices";
						this.foodsList = this.rices;
						break;
					default:
						this.foodsType = "others";
						this.foodsList = this.others;
				}
				console.log(this.foodsType);
			},
		},
	};
</script>

<style lang="less" scoped>
	.container {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 150px;
		background-color: #545c64;
		right: 0;
		overflow: hidden;
		.drawer-container {
			position: absolute;
		}
	}
</style>
