<template>
	<div class="food-list-container">
		<ul class="list-container">
			<li
				class="list-item"
				v-for="(item, index) in foods">
				<el-card
					:body-style="{ padding: '0px' }"
					shadow="always">
					<img
						:src="getUrl(item.title)"
						class="image" />
					<div style="padding: 14px 10px 5px; text-align: center">
						<span class="title">{{ item.title }}</span>
						<div>
							<el-button
								type="text"
								class="button"
								@click="onHandleAdd(item)">
								加入食谱
							</el-button>
						</div>
					</div>
				</el-card>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: {
			foods: {
				type: Array,
			},
			type: {
				type: String,
			},
		},

		data() {
			return {};
		},
		methods: {
			onHandleAdd(value) {
				this.$store.dispatch("addOneFood", value);
				this.$message({
					message: "添加成功！",
					type: "success",
					duration: 1500,
				});
			},
			getUrl(name) {
				return require("@/asset/" + this.type + "/" + name + ".jpg");
			},
		},
	};
</script>

<style lang="less" scoped>
	.food-list-container {
		.list-container {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-around;
			.list-item {
				width: 30%;
				margin: 10px 0px;
				.image {
					width: 100%;
					height: 250px;
					// object-fit: contain;
				}
				.title {
					font-weight: 800;
				}
			}
		}
	}
</style>
