import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		foodsList: [],
	},
	mutations: {
		addOneFood(state, payload) {
			state.foodsList.push(payload);
		},
		deleteOneFood(state, payload) {
			state.foodsList = state.foodsList.filter(
				(item) => item.value !== payload.value
			);
		},
		clearFoodsList(state, payload) {
			state.foodsList = [];
		},
	},
	actions: {
		addOneFood({ commit }, payload) {
			commit("addOneFood", payload);
		},
		deleteOneFood({ commit }, payload) {
			commit("deleteOneFood", payload);
		},
		clearFoodsList({ commit }) {
			commit("clearFoodsList");
		},
	},
});
