<template>
	<div class="main-container">
		<el-tabs
			v-model="activeName"
			type="card">
			<el-tab-pane
				label="选择框抽奖"
				name="first">
				<div class="top">
					<button
						class="start"
						@click="start"
						:disabled="disable">
						开始
					</button>
				</div>
				<ul class="list-container">
					<li
						class="list-item"
						:class="{ active: currentIndex == index }"
						v-for="(item, index) in foodsList"
						:key="index">
						<el-card
							:body-style="{ padding: '0px' }"
							shadow="always">
							<img
								:src="getUrl(item)"
								class="image" />
							<div
								style="
									padding: 14px 10px 5px;
									text-align: center;
								">
								<span class="title">{{ item.title }}</span>
								<div>
									<el-button
										type="text"
										class="button"
										@click="onHandleDelete(item)">
										移除
									</el-button>
								</div>
							</div>
						</el-card>
					</li>
				</ul></el-tab-pane
			>
			<el-tab-pane
				label="自定义输入"
				name="second">
				<div class="input-container">
					<el-button
						type="primary"
						@click="addOne"
						>新增一个菜品</el-button
					>
					<el-button
						type="danger"
						@click="clear"
						>清空</el-button
					>
					<el-button
						type="success"
						@click="inputStart"
						>开始</el-button
					>
				</div>
				<div class="input-list-container">
					<el-card
						class="box-card"
						v-for="(item, index) in inputList"
						:class="{ active: inputIndex === index }"
						:key="index">
						<div>{{ item }}</div>
					</el-card>
				</div>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				currentIndex: -1,
				disable: false,
				hasEnd: false,
				currentItem: {
					title: "",
					value: "",
				},
				inputList: [],
				activeName: "second",
				inputIndex: -1,
				disable1: false,
			};
		},
		computed: {
			foodsList() {
				return this.$store.state.foodsList;
			},
		},
		methods: {
			onHandleDelete(item) {
				this.$store.dispatch("deleteOneFood", item);
			},
			getUrl(item) {
				let typeIndex = item.value.split("-")[0];
				let type = "";
				switch (typeIndex) {
					case "1":
						type = "noodles";
						break;
					case "2":
						type = "donburiRice";
						break;
					case "3":
						type = "rices";
						break;
					default:
						type = "others";
				}
				return require("@/asset/" + type + "/" + item.title + ".jpg");
			},
			start() {
				this.disable = true;
				this.hasEnd = false;
				if (this.foodsList.length < 2) {
					this.$message({
						message: "请多添加几个选项",
						type: "info",
						duration: 1500,
					});
					this.disable = false;
					return;
				}
				let count = 0;
				let randomCount = Math.round(
					Math.random() * this.foodsList.length * 11 +
						this.foodsList.length * 3
				);
				let timer = setInterval(() => {
					this.currentIndex =
						(this.currentIndex + 1) % this.foodsList.length;
					count++;
					if (count > randomCount) {
						clearInterval(timer);
						setTimeout(() => {
							this.disable = false;
							this.hasEnd = true;
							let currentItem = this.foodsList[this.currentIndex];
							this.$alert(
								`
									<div class="alert-container" style="width: 100%;height: 100%;">
										<img
											src="${this.getUrl(currentItem)}"
											class="image"
											style="margin: 10%;width: 80%;height: 300px;object-fit: cover;"/>
										<div style="padding: 0px 10px 5px; text-align: center">
											<span class="title">${currentItem.title}</span>
										</div>
										</div>
									</div>
								`,
								{
									dangerouslyUseHTMLString: true,
									showClose: false,
								}
							);
						}, 1000);
					}
				}, 150);
			},
			end() {
				this.currentIndex = -1;
			},
			addOne() {
				this.$prompt("请输入菜品名称", "提示", {
					confirmButtonText: "确定",
					cancelButtonText: "取消",
					showClose: false,
				})
					.then(({ value }) => {
						this.inputList.push(value);
					})
					.catch(() => {
						this.$message({
							type: "info",
							message: "取消输入",
						});
					});
			},
			clear() {
				this.inputList = [];
			},
			inputStart() {
				if (this.inputList.length < 2) {
					return;
				}
				if (this.disable1) {
					return;
				}
				this.disable1 = true;
				let count = 0;
				let randomCount = Math.round(
					Math.random() * this.inputList.length * 11 +
						this.inputList.length * 3
				);
				let timer = setInterval(() => {
					this.inputIndex =
						(this.inputIndex + 1) % this.inputList.length;
					count++;
					if (count > randomCount) {
						clearInterval(timer);
						setTimeout(() => {
							this.disable1 = false;
							let currentItem = this.inputList[this.inputIndex];
							this.$alert(this.inputList[this.inputIndex], {
								showClose: false,
							});
						}, 1000);
					}
				}, 150);
			},
		},
	};
</script>

